import React from "react";
import { Link } from "gatsby";
import colors from "../utils/colors";
import TagsContainer from "./tags-container";

const PostPreview = ({ post }) => {
  return !!post && (
    <div style={styles.container}>
      <div style={styles.headerContainer}>
        <h3 style={styles.headerStyle}>
          <Link style={styles.linkStyle} to={post.fields.slug}>
            {post.frontmatter.title || post.fields.slug}
          </Link>
        </h3>
        <span style={styles.dateStyle}>{post.frontmatter.date}</span>
      </div>
      <TagsContainer tags={post.frontmatter.tags} />
      <span
        dangerouslySetInnerHTML={{
          __html: post.frontmatter.description || post.excerpt
        }}
      />
    </div>
  );
};

const styles = {
  container: {
    padding: "25px 0",
    borderBottom: `1px dashed ${colors.pureApple}`
  },
  headerContainer: {
    display: "flex",
    flexDirection: "row",
    justifyContent: "space-between"
  },
  headerStyle: {
    marginBottom: 0
  },
  linkStyle: { 
    color: colors.pureApple
  },
  dateStyle: {
    fontSize: "0.83255rem",
    lineHeight: "1.75rem",
    fontStyle: "italic",
    marginRight: 8
  }
};

export default PostPreview;
