import React from "react";
import Tag from "./tag";

const TagsContainer = ({ tags }) => {
  return (
    !!tags && (
      <div style={{ display: "flex", paddingBottom: 8 }}>
        {tags.map((t, i) => (
          <Tag key={`${t}-${i}`} name={t} />
        ))}
      </div>
    )
  );
};

export default TagsContainer;
